<template>
  <ManagerLayout>
    <div class="bg-white shadow overflow-hidden sm:rounded-lg">
      <div class="px-4 py-5 sm:px-6">
        <h3 class="text-lg leading-6 font-medium text-gray-900">User Profile</h3>
        <p class="mt-1 max-w-2xl text-sm text-gray-500">Personal details and points.</p>
      </div>
      <div class="border-t border-gray-200" v-if="user && account">
        <dl>
          <div class="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt class="text-sm font-medium text-gray-500">Username</dt>
            <dd class="mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2">{{ user.username }}</dd>
          </div>
          <div class="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt class="text-sm font-medium text-gray-500">Role</dt>
            <dd class="mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2 capitalize">{{ user.role }}</dd>
          </div>
          <div class="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt class="text-sm font-medium text-gray-500">Status</dt>
            <dd class="mt-1 text-sm text-gray-900 sm:mt-0 sm:col-span-2">
              <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-full" :class="user.status === 'active' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'">
                {{ user.status }}
              </span>
            </dd>
          </div>
          <div class="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt class="text-sm font-medium text-gray-500">Points Balance</dt>
            <dd class="mt-1 text-sm text-indigo-600 font-bold sm:mt-0 sm:col-span-2">{{ account.points }}</dd>
          </div>
        </dl>
      </div>
      <div v-else class="p-4 text-center text-gray-500">Loading...</div>
    </div>
  </ManagerLayout>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue';
import ManagerLayout from '@/layouts/ManagerLayout.vue';

const user = ref<any>(null);
const account = ref<any>(null);

onMounted(async () => {
  try {
    const token = localStorage.getItem('token');
    const apiBase = (import.meta.env.VITE_API_BASE_URL || 'http://localhost:3000').replace(/\/$/, '');
    const res = await fetch(`${apiBase}/manager/profile`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (!res.ok) throw new Error('Failed to fetch profile');
    const data = await res.json();
    user.value = data.user;
    account.value = data.account;
  } catch (err) {
    console.error(err);
  }
});
</script>